/**
* Fetches an career
* @param {String} id - id of career
* @returns {Promise} response - a promise of a career item
*/
export function getCareer(id) {
	if (isLoggedIn()) {
		return axios.get(`/v1/careers/${id}`);
	}

	return Promise.resolve(new Response({
		message: 'Unauthorised'
	}, {
		status: 403,
		statusText: 'Unauthorised'
	}));
}

/**
* Fetches an career
* @param {String} id - id of career
* @returns {Promise} response - a promise of a career item to be deleted
*/
export function deleteCareer(id) {
	if (isLoggedIn()) {
		return axios.delete(`/v1/careers/${id}`);
	}

	return Promise.resolve(new Response({
		message: 'Unauthorised'
	}, {
		status: 403,
		statusText: 'Unauthorised'
	}));
}

/**
* Creates an career
* @returns {Promise} response - a promise of a career to be created
*/
export function createCareer(data) {
	if (isLoggedIn()) {
		return axios.post('/v1/careers', data);
	}
	
	return Promise.resolve(new Response({
		message: 'Unauthorised'
	}, {
		status: 403,
		statusText: 'Unauthorised'
	}));
}


/**
* Updates an career
* @param {String} id - career Id
* @param {String} data - career data object
* @returns {Promise} response - a promise of a career to be updated
*/
export function updateCareer(id, data) {
	if (isLoggedIn()) {
		return axios.patch(`/v1/careers/${id}`, data);
	}
	
	return Promise.resolve(new Response({
		message: 'Unauthorised'
	}, {
		status: 403,
		statusText: 'Unauthorised'
	}));
}

