<template>
	<div>
		<template v-if="loadingCareer">
			<div class="loading">
				<Loader />
			</div>
		</template>
		<template v-else>
			<section class="main-grid__row user-profile">
				<div class="user-profile__card">
					<div class="user-profile__primary">
						<h3 class="user-profile__name">Career Detail Section</h3>
					</div>
					<div class="user-profile__item">
						<h5 class="user-profile__section__title">Career Name:</h5>
						<p>{{ itemData.name }}</p>
					</div>
					<div v-if="getLoggedUser.category === 'administrator'">
						<button @click="deleteCareer" class="btn btn__danger">Delete</button>
						<Loader v-if="deletingCareer" />
					</div>
				</div>

				<div class="user-profile__tabs">
					<ul class="nav nav-tabs" id="myTab" role="tablist">
						<li class="nav-item">
							<a class="nav-link active" id="activity-tab" data-toggle="tab" href="#activity" role="tab" aria-controls="activity" aria-selected="false">Edit Career</a>
						</li>
					</ul>
					<div class="tab-content" id="myTabContent">
						<div class="tab-pane fade show active user-profile__tabs__section" id="activity" role="tabpanel" aria-labelledby="activity-tab">
							<div class="settings">
								<form
									method="post"
									@submit.prevent="updateCareer">
									<div class="settings__field">
										<div class="form-row">
											<div class="form-group">
												<div class="settings__value">
													<label for="jobTitle">Name</label>
													<input
														:class="[{'is-invalid': errors.name}]"
														@keydown="clearErrors($event.target.name)"
														v-model="itemData.name"
														name="title"
														value=""
														type="text"
														class="form-control"
														id="jobTitle">
												</div>
												<div class="invalid-feedback">
													{{ errors.name }}
												</div>
											</div>
										</div>
									</div>
									<div class="settings__primary">
										<button
											class="btn btn__save"
											type="button"
											name="button"
											@click.prevent="updateCareer"
											v-bind="{'disabled': loading}">
											Save
										</button>
										<Loader  v-if="loading"/>
									</div>
								</form>
							</div>
						</div>
					</div>
				</div>
			</section>
		</template>
	</div>
</template>

<script>
import { mapGetters } from 'vuex';
import Loader from '../../components/Loader';
import form from '../../mixins/form';
import { updateCareer, getCareer, deleteCareer } from '../../utils/api/career';
import { success, fail } from '../../utils/toast';
import classes from '../../utils/validations/classes';
import Pagination from '../../components/Pagination';

export default {
	name: 'Career',
	mixins: [form],
	data() {
		return {
			itemData: {},
			errors: {},
			loadingCareer: true,
			careerLoaded: false,
			loading: false,
			classes: [],
			loadingClasses: true,
			classesLoaded: false,
			deletingCareer: false,
			limit: 10,
			offset: 0,
			total: 0,
			baseUrl: ''
		};
	},
	created() {
		this.getCareer();
		this.limit = this.$route.query.limit ? this.$route.query.limit : this.limit;
		this.offset = this.$route.query.offset ? this.$route.query.offset : this.offset;
		this.baseUrl = `career/${this.$route.params.id}/detail`;
	},
	methods: {
		deleteCareer() {
			this.deletingCareer = true;
			deleteCareer(this.$route.params.id)
				.then((response) => {
					if (response.status === 200) {
						this.deletingBadge = false;
						success({
							title: 'Success! Career has been deleted!'
						});
						setTimeout(() => {
							this.$router.push('/careers');
						}, 500);
						return true;
					}
					return false;
				})
				.catch((error) => {
					this.deletingCareer = false;
					fail({
						title: 'Ooops! Something went wrong. Pls try again.'
					});
					return error;
				});
			return true;
		},
		getCareer() {
			getCareer(this.$route.params.id)
				.then((response) => {
					if (response.status === 200) {
						this.itemData = response.data.data;
						this.loadingCareer = true;
						return true;
					}
					return false;
				})
				.catch((error) => {
					this.careerLoaded = false;
					return error;
				})
				.finally(() => {
					this.loadingCareer = false;
				});
			return true;
		},
		updateCareer() {
			const data = this.itemData;
			['created_at', 'updated_at', 'id', 'career_segment_id'].forEach((key) => {
				delete data[key];
			});
			this.validate(data, classes.categories);
			if (Object.keys(this.issues).length) {
				this.errors = this.issues;
				return false;
			}
			this.loading = true;
			updateCareer(this.$route.params.id, this.itemData)
				.then((response) => {
					if (response.status === 200) {
						this.loading = false;
						success({
							title: 'Success! Career has been updated!'
						});
					}
				})
				.catch((error) => {
					this.loading = false;
					fail({
						title: 'Ooops! Something went wrong. Pls try again.'
					});
					return error;
				});
			return true;
		}
	},
	components: {
		Pagination,
		Loader
	},
	computed: {
		...mapGetters([
			'getLoggedUser'
		])
	}
};
</script>

